<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="试玩赚钱（付费）- 奖励未到账"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content qk-complain-subtask">
      <div class="top-notice">为了处理更高效，请确保以下信息的真实准确。</div>

      <list class="list-android">
        <hl-item class="text-input">
          <div class="sub-title">时间</div>
          <div class="von-input-wrapper">
            <label class="item-input thin-border von-input">
              <input @change="selectDate" v-model="info.task_date" type="date" required aria-required="true"
                     data-placeholder="任务时间"/>
            </label>
          </div>
        </hl-item>
        <hl-item class="text-input">
          <div class="sub-title">名称</div>
          <select v-model="info.task_id" :class="{'select-color': option_selected}">
            <option v-for="option in tasks"
                    v-bind:selected="option.selected"
                    v-bind:disabled="option.disabled"
                    v-bind:value="option.id">
              {{ option.title }}
            </option>
          </select>
        </hl-item>
        <hl-item class="text-input">
          <div class="sub-title">姓名</div>
          <!--<von-input type="text" v-model="info.itunes_account" placeholder="iTunes账号姓名"></von-input>-->
          <input type="text" v-model="info.itunes_account" placeholder="iTunes账号姓名">
        </hl-item>
        <hl-item class="text-input">
          <div class="sub-title">账号</div>
          <!--<von-input type="text" ref="accountFontSize" :style="{fontSize: accountFontSize + 'px'}" v-model="info.itunes_email" placeholder="iTunes账号"></von-input>-->
          <input type="text" ref="accountFontSize" :style="{fontSize: accountFontSize + 'px'}" v-model="info.itunes_email" placeholder="iTunes账号">
        </hl-item>
        <hl-item class="hl-item-borderless-bottom text-upload">
          <div class="title">上传截图</div>
          <div class="item-note">
            <div class="note-require">请上传苹果付费凭证邮件全屏截图</div>
            <div class="note-hint">（苹果一般在用户付费完成一个工作日后发送邮件凭证，如果未收到邮件，可联系苹果客服再次发送）</div>
          </div>
          <div class="upload">
            <div class="image-area">
              <div v-if="info.image" class="preview"><img :src="info.image"></div>
              <div class="upload-btn">
                <input type="file" @change="doUpload($event.target)">
              </div>
            </div>
            <div class="showcase">
              <div @click="showPhoneSample()">手机版样张</div>
              <div class="last" @click="showComputerSample()">电脑版样张</div>
            </div>
          </div>
        </hl-item>
      </list>

      <div style="padding: 10px 15px 15px 15px;">
        <button :class="{'button-active' : !buttonDisabled}" class="button button-block button-assertive" @click="submit()">
          提交
        </button>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
  @import '../../../scss/mixins';

  .qk-complain-subtask {
    /*padding-top: 50px;*/
    margin-top: 50px;
    background-color: #fff !important;

    @include font-regular()

    .list-android {
      padding: 0 20px;
    }

    .top-notice {
      height: 38px;
      padding: 20px 20px 0;
      font-size: 13px;
      color: #B5B5B5;
      letter-spacing: 0.11px;
    }

    .item {
      padding: 0 !important;
      font-size: 14px;
      margin: 0;
    }

    .text-id {
      color: #484746;
      line-height: 50px;
    }

    .text-input {
      height: 74px;

      .sub-title {
        height: 17px;
        line-height: 17px;
        font-size: 12px;
        color: #B5B5B5;
        margin-top: 20px;
      }

      .von-input {
        width: 200px;
        color: #B5B5B5;
        height: 37px;
        min-height: 37px;
        margin: 0;
        padding: 0;
      }

      .von-input::after {
        border: 0;
      }

      input[type='date'] {
        -webkit-appearance: none;
      }

      input[type="date"]::before {
        content: attr(data-placeholder);
        width: 100%;
        color: #DDDDDD;
      }

      input[type="date"]:focus::before,
      input[type="date"]:valid::before {
        display: none;
      }

      input {
        transition: font-size .1s ease-in;
        padding: 17px 0;
        color: #3a3a3a;
        height: 37px;
        line-height: 36px;
        padding: 0;
        width: 200px;
        border-bottom: 1px solid #DDDDDD;
        font-size: 24px;
        border-radius: 0;

        @include font-regular()

        &::-webkit-input-placeholder {
          height: 36px;
          line-height: 36px;
          color: #DDDDDD;
        }
      }

      select {
        -webkit-appearance: none;
        padding: 0;
        border: 0;
        color: #DDDDDD;
        background-color: #FFFFFF;
        height: 37px;
        line-height: 37px;
        width: 200px;
        font-size: 24px;
        border-bottom: 1px solid #DDDDDD;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;

        &.select-color {
          color: #3a3a3a;
        }
      }
    }

    .text-upload {
      padding-top: 35px !important;
      color: #666666;

      .title {
        height: 22px;
        line-height: 22px;
        font-size: 16px;
        color: #3A3A3A;
      }

      .item-note {
        float: initial;
        font-size: 13px;
        color: #888888;
        letter-spacing: 1px;
        line-height: 22px;

        .note-require {
          height: 22px;
          margin: 12px 0 10px;
          line-height: 1.69;
        }

        .note-hint {
          margin-bottom: 20px;
          white-space: pre-wrap;
          line-height: 1.69;
          height: 44px;
        }
      }

      .upload {
        float: left;
        width: 100%;
        height: 50px;

        .image-area {
          float: left;

          .preview {
            width: 50px;
            height: 50px;
            margin-right: 10px;
            float: left;

            img {
              width: 50px;
              height: 50px;
              padding: 0;
              margin: 0;
            }
          }

          .upload-btn {
            float: left;
            width: 50px;
            height: 50px;
            background: url("//assets.qkcdn.com/images/2bfa8934bf559c1550f052e282b06924.png") no-repeat top center;
            background-size: 100% 100%;

            input {
              width: 50px;
              height: 50px;
              opacity: 0;
            }
          }
        }

        .showcase {
          text-align: right;
          float: right;
          line-height: 50px;

          div {
            float: left;

            @include font-regular();

            font-size: 13px;
            color: #3A3A3A;

            &.last {
              margin-left: 19px;
            }
          }

          div::after {
            content: '';
            display: inline-block;
            vertical-align: middle;
            width: 0;
            height: 0;
            border-top: 3px solid transparent;
            border-bottom: 3px solid transparent;
            border-left: 4px solid #3A3A3A;
            margin-left: 5px;
            margin-bottom: 2px;
          }
        }
      }
    }

    .button {
      border-radius: 2px;
      width: 172px;
      height: 40px;
      line-height: 40px;
      min-height: 40px;
      font-size: 13px;
      margin-top: 0;
      background: #DDDDDD;

      @include font-semibold()
    }

    .button.disabled, .button[disabled] {
      opacity: 1;
    }

    .button-active {
      background: #EF4F2A;
    }

    .hairline-bottom::after,
    .hairline-top::before {
      visibility: hidden;
    }
  }
  /*加了个 complain-modal 防止样式污染全局modal*/
  .modal-backdrop.complain-modal {
    .bar.bar-header {
      background: #F5F5F5;
      height: 50px;
      text-align: left;
      box-shadow: none;

      @include font-semibold()
    }

    .page.has-navbar .page-content {
      padding-top: 50px;
    }

    .bar .title {
      height: 50px;
      line-height: 50px;
      text-align: left;
      margin: 0 20px;

      @include font-semibold()
    }

    .bar .title+.button:last-child{
      width: 60px;
      height: 50px;
      background-color: #f0f0f0;
      top: 0;
      right: 0;
    }

    .ion-ios-close-empty:before {
      content: '';
      background: url("//assets.qkcdn.com/images/d04626874955af7ce9fc4a974a4b76ab.png") no-repeat;
      background-size: 100%;
      display: inline-block;
      width: 18px;
      height: 18px;
      margin-top: 6px;
    }
  }

  .von-input-wrapper .input-clear {
    display: none !important;
  }
</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index'
  import {mapActions} from 'vuex'
  import PhoneSample from './modals/PhoneSample'
  import ComputerSample from './modals/ComputerSample'

  export default {
    components: {
      AnimateHeader
    },

    data () {
      return {
        info: {
          task_date: '',
          task_id: '',
          itunes_account: '',
          itunes_email: '',
          image: ''
        },
        tasks: [
          {id: '', title: '选择待申诉任务', selected: true, disabled: true}
        ],
        phoneSampleModal: null,
        computerSampleModal: null,
        option_selected: false
      }
    },

    watch: {
      'info.task_id': function (v) {
        if (v) {
          this.option_selected = true
        } else {
          this.option_selected = false
        }
      }
    },

    computed: {
      buttonDisabled () {
        let info = Object.assign({}, this.info)
        for (let key in info) {
          if (info[key] === '') {
            return true
          }
        }
        return false
      },

      accountFontSize () {
//        let $input = this.$refs['accountFontSize'] && this.$refs['accountFontSize'].$el.querySelector('input')
        let len = this.info.itunes_email.length
        let fontSize = 24
        if (len >= 16 && len < 24) {
          fontSize = 16
        }
        if (len >= 24 && len < 27) {
          fontSize = 14
        }
        if (len >= 27 && len < 29) {
          fontSize = 13
        }
        if (len >= 29 && len < 32) {
          fontSize = 12
        }
//        $input && ($input.style.fontSize = fontSize + 'px')
        return fontSize
      }
    },

    created () {
      $modal.fromComponent(PhoneSample, {
        title: '手机版样张',
        theme: 's'
      }).then(modal => {
        this.phoneSampleModal = modal
      })

      $modal.fromComponent(ComputerSample, {
        title: '电脑版样张',
        theme: 'd'
      }).then(modal => {
        this.computerSampleModal = modal
      })

      this.$nextTick(() => {
        document.querySelectorAll('.modal-backdrop').forEach((el) => {
          console.log(el)
          el.classList.add('complain-modal')
        })
      })
    },

    destroyed () {
      if (this.phoneSampleModal) $modal.destroy(this.phoneSampleModal)
      if (this.computerSampleModal) $modal.destroy(this.computerSampleModal)
    },

    methods: {
      ...mapActions('help', [
        'getSubTaskByDate',
        'submitSubtaskInfo',
        'upload'
      ]),

      selectDate () {
        this.getSubTaskByDate(this.info.task_date).then((tasks) => {
          let data = [{
            id: '',
            disabled: true,
            selected: true,
            title: '选择待申诉任务'
          }]

          if (tasks.length) {
            for (var v of tasks) {
              v.disabled = false
              v.selected = false
              data.push(v)
            }

            this.tasks = data
            this.info.task_id = this.tasks[1].id
          } else {
            this.tasks = data
            this.info.task_id = this.tasks[0].id
            $toast.show('选择的日期上并没有开始任务')
          }
        })
      },

      submit () {
        if (!this.info.task_date) {
          $toast.show('没有选择任务时间！')
          return
        }
        if (!this.info.task_id) {
          $toast.show('没有选择任务！')
          return
        }
        if (!this.info.itunes_account) {
          $toast.show('没有填写iTune账号姓名！')
          return
        }
        if (!this.info.itunes_email) {
          $toast.show('没有填写iTune账号！')
          return
        }
        if (!this.info.image) {
          $toast.show('没有上传截图！')
          return
        }
        this.submitSubtaskInfo(this.info).then(() => {
          $toast.show('提交成功')
          $router.back('/help')
        })
      },

      doUpload (file) {
        this.upload(file).then((payload) => {
          let uploadData = payload.files['0']
          let file = uploadData.hash + '.' + uploadData.ext
          let uploadUrl = payload.uri_prefix + '/' + file
          this.info.image = uploadUrl
        }, (err) => {
          $toast.show(err)
        })
      },

      showComputerSample () {
        this.computerSampleModal.show()
        this.$nextTick(() => {
          document.querySelectorAll('.modal-backdrop').forEach((el) => {
            el.classList.add('complain-modal')
          })
        })
      },

      showPhoneSample () {
        this.phoneSampleModal.show()
        this.$nextTick(() => {
          document.querySelectorAll('.modal-backdrop').forEach((el) => {
            el.classList.add('complain-modal')
          })
        })
      },

      back () {
        let path = _.get(this.$route.query, 'backroute', '/help/self')
        $router.back({path})
      }
    }
  }
</script>



